import PerfectScrollbar from 'perfect-scrollbar';

$('.js-main-menu')
	.each((index, component) => {
		let $body = $(document.body);
		let $component = $(component);
		let $closeBtn = $component.find('.js-close-menu');
		let $listBtn = $component.find('.js-menu-list-btn');

		$listBtn.on('click', (e) => {
			let $trgt = $(e.currentTarget);
			let $parentList = $trgt.closest('.js-menu-list');
			let $listItem = $trgt.closest('.menu-list__item').find('.menu-list');
			let $btnArr = $parentList.find('.js-menu-list-btn');
			let $listArr = $parentList.find('.menu-list');

			if (!$trgt.hasClass('menu-list__btn_active')) {
				$btnArr.removeClass('menu-list__btn_active');
				$trgt.addClass('menu-list__btn_active');

				$listArr.removeClass('menu-list_active');
				$listItem.eq(0).stop(true, true).addClass('menu-list_active');
			} else {
				window.location = e.target.href;
			}
		});

		function hideLists() {
			$component.find('.menu-list').removeClass('menu-list_active');
		}

		$body.on('main-menu:open', (e) => {
			$component.addClass('main-menu_active');
		});

		$body.on('main-menu:close', () => {
			$component.removeClass('main-menu_active');

			hideLists();
		});

		$closeBtn.on('click', (e) => {
			e.preventDefault();

			$body.trigger('main-menu:close');
		});
	});
